  .section--hero .hero-divider {
    position: absolute;
    bottom: -20px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; }
    .section--hero .hero-divider .divider {
      width: 100%; }
    .section--hero .hero-divider .scroll-down {
      position: absolute;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
