@charset "UTF-8";
.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--white);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
  transition: all 200ms ease-in-out; }
  .main-nav .nav-menu {
    display: flex;
    align-items: center;
    height: 100%; }
  .main-nav .nav-item {
    position: relative;
    overflow: hidden;
    height: 100%;
    padding: 0 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: var(--dark);
    text-decoration: none;
    opacity: .75;
    transition: all 200ms ease-in-out; }
    .main-nav .nav-item--logo {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 15px;
      padding-right: 15px;
      opacity: 1; }
      .main-nav .nav-item--logo .nile-typo {
        height: 32px;
        filter: invert(100%);
        transition: all 200ms ease-in-out; }
      .main-nav .nav-item--logo .logo-wrapper {
        height: 40px;
        width: 40px;
        border-radius: 100px;
        margin-right: 20px;
        position: relative;
        filter: invert(100%);
        transition: all 200ms ease-in-out; }
        .main-nav .nav-item--logo .logo-wrapper img.logo {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          transition: all 200ms ease-in-out; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-1  {
            opacity: 1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-2  {
            opacity: .1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-3  {
            opacity: .1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-4  {
            opacity: .1 !important; }
    .main-nav .nav-item:before {
      content: '';
      position: absolute;
      bottom: -5px;
      height: 10px;
      width: 0;
      border-radius: 10px;
      transition: all 200ms ease-in-out; }
    .main-nav .nav-item:hover, .main-nav .nav-item.active {
      opacity: 1;
      background-color: rgba(0, 0, 0, 0.05);
      color: var(--secondary-2); }
      .main-nav .nav-item:hover:before, .main-nav .nav-item.active:before {
        content: '';
        position: absolute;
        bottom: -5px;
        height: 10px;
        width: 50px;
        border-radius: 10px;
        background-color: var(--secondary-2); }
    .main-nav .nav-item:hover {
      cursor: pointer; }
      .main-nav .nav-item:hover:before {
        width: 10px; }
  .main-nav.top {
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: none; }
    .main-nav.top .nav-item {
      color: var(--white); }
      .main-nav.top .nav-item--logo .logo-wrapper {
        filter: invert(0%); }
      .main-nav.top .nav-item--logo .nile-typo {
        filter: invert(0%); }
      .main-nav.top .nav-item:hover, .main-nav.top .nav-item.active {
        background-color: rgba(255, 255, 255, 0.1); }

img.logo.logo-rotation-2 {
  opacity: .2; }

img.logo.logo-rotation-3 {
  opacity: .2; }

img.logo.logo-rotation-4 {
  opacity: .2; }
